.Dropdown {
  display: block;
  position: relative;
  width: 400px;
  padding: 20px;
  border: 1px solid black;
}

.Dropdown__toggle {
  background-color: #f6f7efc7;
  color: #000000;
  padding: 4px;
  margin: 5px;
  cursor: pointer;
}

.Dropdown__menu {
  position: absolute;
  top: 100%;
  left: 0;
  width: 400px;
  padding: 20px;
  text-align: center;
  background-color: teal;
  color: white;
}
